.acf-block-preview .image-gallery {
  pointer-events: none;
}
.acf-block-preview .image-gallery .gallery {
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -moz-flex-direction: row;
  -webkit-flex-direction: row;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.acf-block-preview .image-gallery .gallery a {
  display: block;
  min-width: 200px;
}
.acf-block-preview .image-gallery .gallery-item {
  margin: 0 25px;
  position: relative;
}
.acf-block-preview .image-gallery .gallery-item-image .color_filter {
  display: none;
}

.no-js .image-gallery {
  display: none;
}

.image-gallery {
  margin-top: 3rem;
  margin-bottom: 3rem;
  position: relative;
}
.image-gallery .gallery_slider-controls {
  display: none;
}
.image-gallery .gallery-item {
  margin: 0 25px;
  position: relative;
  overflow: hidden;
}
.image-gallery .gallery-item-image {
  position: relative;
  padding-bottom: 100%;
}
.image-gallery .gallery-item-image .color_filter {
  display: none;
}
.image-gallery .gallery-item-image img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: 50% 50%;
     object-position: 50% 50%;
  font-family: "object-fit";
}
.image-gallery .gallery-item-description {
  position: absolute;
  z-index: 20;
  bottom: -400px;
  padding: 20px;
  background: rgba(23, 121, 186, 0.8);
  text-align: center;
  -webkit-transition: bottom 0.3s;
  transition: bottom 0.3s;
  width: 100%;
}
.image-gallery .gallery-item-description p {
  font-size: 1.25rem;
  color: #fefefe;
  margin: 0;
}
.image-gallery .gallery-item:hover .gallery-item-description {
  bottom: 0;
}

@media print, screen and (min-width: 64em) {
  .image-gallery {
    margin-top: 5rem;
    margin-bottom: 5rem;
  }
  .image-gallery .gallery-item .color_filter {
    position: absolute;
    width: 100%;
    height: 100%;
    background: #1779ba;
    background: rgba(23, 121, 186, 0.7);
    z-index: 100;
    display: block;
  }
  .image-gallery .gallery-item.slick-active .color_filter {
    display: none;
  }
  .image-gallery .gallery_slider-controls {
    position: absolute;
    top: 50%;
    z-index: 10;
    width: 100%;
    display: block;
  }
  .image-gallery .gallery_slider-controls .gal-prev {
    left: 20px;
  }
  .image-gallery .gallery_slider-controls .gal-next {
    right: 20px;
  }
  .image-gallery .gallery_slider-controls .gal-prev, .image-gallery .gallery_slider-controls .gal-next {
    position: absolute;
    z-index: 20;
  }
  .image-gallery .gallery_slider-controls .gal-prev svg, .image-gallery .gallery_slider-controls .gal-next svg {
    height: 50px;
    cursor: pointer;
  }
  .image-gallery .gallery_slider-controls .gal-prev svg path, .image-gallery .gallery_slider-controls .gal-next svg path {
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s;
    fill: #fefefe;
    opacity: 0.8;
  }
  .image-gallery .gallery_slider-controls .gal-prev svg:hover path, .image-gallery .gallery_slider-controls .gal-next svg:hover path {
    opacity: 0.5;
  }
}